body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wrapper {
    padding-top: 50px;
    background: rgb(78, 115, 139);
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }
  
.titleBox {
    text-align: center;
    width: 100%;
    color: #FFDE00;
    text-shadow: 
        -3px -3px 0 #3B4CCA,
        3px -3px 0 #3B4CCA,
        -3px 3px 0 #3B4CCA,
        3px 3px 0 #3B4CCA;
    font-family: Pokemon Solid;
    /* 
    Font from: 
    https://fontmeme.com/pokemon-font/ 
    */
}

.titleBox h1 {
    margin-top: 90px;
    font-size: 3.5em;
}
.card {
    border-radius: 2px;
    height: 250px;
    margin: 1rem;
    position: relative;
    width: 250px;
    text-align: center;
  }
  
  
  .card > .img-container > .pokedex-entry .pokedex-hover > img {
    width: 100%;
    align-content: center;
  }

  .card:hover {
    background-color: grey;
  }
  .dexter {
    width: 75%;
    align-content: center;
  }
  

.navBarThingy {
    position: fixed;
    top: 0;
    left: 0;
    background: #000;
    min-height: 65px;
    width: 100%;
    color: #fff;
    font-size: 2em;
    font-weight: bold;
    z-index: 99;
    list-style-type: none;
    text-align: center;
}


#right-wrong {
    text-align: center;
}

#scoreTick {
    text-align: right;
    font-family: 'Times New Roman', Times, serif;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

